.AppContainer {

}

.AppContainer-logo {
  -webkit-animation: AppContainer-logo-spin infinite 20s linear;
          animation: AppContainer-logo-spin infinite 20s linear;
  height: 80px;
}

.AppContainer-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.AppContainer-title {
  font-size: 1.5em;
}

.AppContainer-intro {
  font-size: large;
}

@-webkit-keyframes AppContainer-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes AppContainer-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



